.main{
    width: 100%;
    height: 100%;
    background-image: url('../images/Background2.svg');
    background-size: cover;
}

body{
    margin: 0;
    background-image: linear-gradient(0deg, #121414, black)
}

@font-face {
    font-family: Bariol;
    src: url("../fonts/Bariol.eot"); /* IE9 Compat Modes */
    src: url("../fonts/Bariol.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
         url("../fonts/Bariol.woff") format("woff"), /* Modern Browsers */
         url("../fonts/Bariol.ttf")  format("truetype"), /* Safari, Android, iOS */
         url("../fonts/Bariol.svg#svgFontName") format("svg"); /* Legacy iOS */
}

* {
    font-family: Bariol;
    letter-spacing: 0.2em;
}

i {
    letter-spacing: 0;
}